import {uniPopup} from "@dcloudio/uni-ui/lib/uni-popup/uni-popup.vue" 
export default {
	data() {
		return {
			background: ['red', 'blue', 'pink'],
			indicatorDots: true,
			indicator1:false,
			autoplay: true,
			interval: 2000,//间隔
			duration: 500,//
			mark:"",
			id:"",
			supplyList:[],
			collection:1,
			shrink:[],
			phone:"",
			branch:0,
			tradeId:0,
			checkTitle:false
		}
	},
	onLoad:function(option){
		var _self = this
		console.log(option)
		_self.mark = option.mark
		_self.id = option.id
		
		_self.getInfo()
		_self.checkAuth()
	},
	onShow:function(){
			var _self = this
		
	},
	methods: {
		previewImage: function(e) {
			uni.previewImage({
				current: e,
				urls: this.shrink
			})
		},
		/**
		 * 判断是否验证
		 */
		checkAuth:function(){
			var _self = this
			uni.request({ 
				url:_self.$api+"dockingManager/judgeCard",
				data:{optionId:uni.getStorageSync("openId")},
				method:"GET",
				success:function(res){
					console.log(res)
					if(res.data==0){
						_self.checkTitle = false
					}
					if(res.data==1){
						_self.checkTitle = true
					}
				}
			})
			
		},
		apply:function(){
			var _self = this
			if(_self.checkTitle){
				uni.makePhoneCall({
				phoneNumber: _self.details.phone //仅为示例
				});
			}else{
				uni.showToast({
					title:"您尚未进行认证，请先进行认证",
					icon:"none"
				})
				return false
			}
		},
		hidePopup:function(){
			this.showPopupMiddleImg = false
		},
		hidePopup1:function(){
			this.showPopupMiddleImg1 = false
		},
		getInfo:function(){
			var _self = this
			uni.request({ 
				url:_self.$api+"dockingManager/declareQuery",
				data:{id:_self.id,mark:_self.mark,optionId:uni.getStorageSync("openId")},
				method:"GET",
				success:function(res){ 
					console.log(res)
					if(res.data[0].shrink == ''||res.data[0].shrink==null || res.data[0].shrink==undefined){
						res.data[0].shrink = ''
					}else{
						_self.shrink = res.data[0].shrink.split(',')
					}
					console.log(_self.shrink)
					_self.phone = res.data[0].phone
					_self.supplyList = res.data[0]
					if(res.data[0].state === undefined || res.data[0].state===null){
						_self.collection =1
					}else{
						_self.collection =2
					}
				}
			})   
		},
		/**
		 * 收藏
		 */
			collect:function(){
			var _self = this
			if(_self.collection == 1){
				uni.request({
					url:_self.$api+"dockingManager/hideAdd",
					data:{id:_self.id,mark:0,optionId:uni.getStorageSync("openId")},
					success:function(res){
						console.log(res)
						if(res.data==1){
							console.log("收藏成功")
							_self.collection = 2
							return 
						}
						if(res.data==99){
							uni.showModal({
								title: '提示',
								content: '此操作需用户授权，是否进行授权',
								success: function (res) {
									if (res.confirm) {
										//跳转到授权页面  
										uni.navigateTo({
											url:"../../login/login"  
										})
										console.log('用户点击确定');
									} else if (res.cancel) {
										console.log('用户点击取消');
									}
								}
							});
							return false
						}
						if(res.data == 98){
								uni.showToast({
								title:"您尚未进行认证，请先进行认证",
								icon:"none"
							})
							return false
						}
						else{
							uni.showToast({
								title:"收藏失败",
								icon:"none"
							})
						} 
					}
				})
			}else{
					uni.request({
					url:_self.$api+"dockingManager/hideDelete",
					data:{id:_self.id,optionId:uni.getStorageSync("openId")},
					success:function(res){
						console.log(res)
						if(res.data==1){
							console.log("取消收藏成功")
							_self.collection = 1
							return
						}
						if(res.data==99){
							uni.showModal({
								title: '提示',
								content: '此操作需用户授权，是否进行授权',
								success: function (res) {
									if (res.confirm) {
										//跳转到授权页面  
										uni.navigateTo({
											url:"../../login/login"  
										})
										console.log('用户点击确定');
									} else if (res.cancel) {
										console.log('用户点击取消');
									}
								}
							});
							return false
						}
						else{
							uni.showToast({
								title:"取消收藏失败",
								icon:"none"
							})
						}						
					},
					})
			}
		},
		/**
		 * 转发
		 */  
		onShareAppMessage: function (options) { 
				var _self = this
				if (options.from === 'button') {
				  // 来自页面内转发按钮
				  console.log(options.target)
				}
				return {
				  //## 此为转发页面所显示的标题
				  //title: '好友代付', 
				  //## 此为转发页面的描述性文字
				  desc: _self.supplyList.name, 
				  //## 此为转发给微信好友或微信群后，对方点击后进入的页面链接，可以根据自己的需求添加参数
				  path:  'pages/infoPage/infoDetail/infoDetail?id='+_self.id+'&mark='+_self.mark, 
				  //## 转发操作成功后的回调函数，用于对发起者的提示语句或其他逻辑处理
				  success: function(res) {
					  uni.request({
					  url:_self.$api+"dockingManager/hideAdd",
					  data:{id:_self.id,mark:1,optionId:uni.getStorageSync("openId")},
					  success:function(res){
						  console.log(res)
						  console.log("转发成功")
					  },
					  })  
				  },
				  //## 转发操作失败/取消 后的回调处理，一般是个提示语句即可
				  fail: function() {
					  console.log("error")
				  }
				}
		},
	},
	 components: {uniPopup},
}
